import React, { Component } from 'react';
import { 
    Container, 
    Navbar, 
    Button, 
    Form,
    Table,
    Col,
    Row,
    Alert } from 'react-bootstrap';

import './App.css';
import api from './Api';

class App extends Component {
    state = {
        status_message: '',
        user_City: '',
        user_MobileNo: '',
        all_user_data: [],
        current_user: ''
    };

    componentDidMount() {
        this.getUserData();
    }

    getUserData () {
        api({
            method: 'get',
            url: '/GetData?email=' + this.state.current_user
        }).then((response) => {
            if(response.data[0])
            {
                this.setState({
                    user_City: response.data[0].City,
                    user_MobileNo: response.data[0].MobileNo,
                    status_message: 'Data about the logged in user recevied successfully .'
                });
            }
           
        })
        .catch((error) => {
            this.setState({status_message: 'Error getting data about logged in user.'});
        });
    };

    saveUserData() {
        api({
            method: 'put',
            url: '/SaveData?email=' + this.state.current_user,
            data: {
                City: this.state.user_City,
                MobileNo: this.state.user_MobileNo
            }
        }).then((response) => {
            this.setState({status_message: 'Data about the user saved successfully.'});
        })
        .catch((error) => {
            console.log(error);
            this.setState({status_message: 'Error saving data about the user.'});
        });
    }

    deleteUserData() {
        api({
            method: 'delete',
            url: '/DeleteData?email=' + this.state.current_user
        }).then((response) => {
            this.setState({
                user_City: '',
                user_MobileNo: '',
                status_message: 'Data about the user deleted successfully.'
            });
        })
        .catch((error) => {
            this.setState({status_message: 'Error deleting data about the user.'});
        });
    }

    getAllUserData () {
        api({
            method: 'get',
            url: '/GetAllData'
        }).then((response) => {
            this.setState({
                all_user_data: response.data,
                status_message: 'Data about all users received successfully.'
            })
        })
        .catch((error) => {
            this.setState({status_message: 'Error getting data about logged in user.'});
        });
    };

    render() {
        return (
            <Container>
                <Navbar bg="dark" variant="dark" className="justify-content-between">
                    <Navbar.Brand>
                        <b>Azure Serverless</b>
                    </Navbar.Brand>
                    
                    <Navbar.Text>
                        Logged in as: {this.state.current_user}
                    </Navbar.Text>
                </Navbar>

                <Form className="box">
                    <Form.Group as={Row} controlId="formPlaintextEmail">
                        <Form.Label column sm="2">
                        Email
                        </Form.Label>
                        <Col sm="10">
                          <Form.Control placeholder="Enter your Email" defaultValue={this.state.current_user} onChange={(event) => { this.setState({ current_user: event.target.value,}); }}/>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row}>
                        <Form.Label column sm="2">
                            City
                        </Form.Label>
                        <Col sm="10">
                            <Form.Control placeholder="Enter your City" value={this.state.user_City || ''} onChange={(event) => { this.setState({ user_City: event.target.value,}); }}/> 
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row}>
                        <Form.Label column sm="2">
                            MobileNo
                        </Form.Label>
                        <Col sm="10">
                            <Form.Control placeholder="Enter your MobileNo" value={this.state.user_MobileNo || ''} onChange={(event) => { this.setState({ user_MobileNo: event.target.value,}); }}/>
                        </Col>
                    </Form.Group>
                    <div className="buttonContainer">
                    <Button variant="light" className="buttonGap" onClick={() => this.getUserData()}>
                            Get Info
                        </Button>
                        <Button variant="dark" className="buttonGap" onClick={() => this.saveUserData()}>
                            Save Info
                        </Button>
                        <Button variant="danger" className="buttonGap" onClick={() => this.deleteUserData()} >
                            Delete Info
                        </Button>
                        <Button variant="info">
                            Log Out
                        </Button>
                    </div>
                </Form>

                <Alert variant='info'>
                    Status: {this.state.status_message}
                </Alert>
                
                <div className="box boxGap">
                    <div className="buttonContainer">
                        <Button variant="success" onClick={() => this.getAllUserData()}>
                            Get Details
                        </Button>
                    </div>
                    <Table striped bordered hover>
                        <thead>
                            <tr>
                                <th>User</th>
                                <th>MobileNo</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.all_user_data.map(( listValue, index ) => {
                                    return (
                                        <tr key={index}>
                                            <td>{listValue.email}</td>
                                            <td>{listValue.MobileNo}</td>
                                        </tr>
                                    );
                                })
                            }
                        </tbody>
                    </Table>
                </div>
            </Container>
        )
    };
};

export default App;
